/* ===== 헤더 ===== */

.header {
	overflow: hidden;
    width: 100%;
    height: 150px;
    position: absolute;
    left: 0;
    top: 0;
}

.header2 {
    transition: all 0.5s;
    z-index: 997;
    padding: 30px 20px;
    overflow: hidden;
    width: 100%;
    height: 150px;
    position: fixed;
    left: 0;
    top: 0;
}


.header-line {
    background-image: linear-gradient(180deg, rgba(112.62499898672104, 215.40455907583237, 255, 1) 0%,rgba(112.62499898672104, 215.40455907583237, 255, 0) 100%);
    width: 100%;
    height: 150px;
    position: absolute;
    left: 0;
    top: 0;
}

.header-line2 {
	display: flex;
	background-image: linear-gradient(180deg, rgba(112.62499898672104, 215.40455907583237, 255, 1) 0%,rgba(112.62499898672104, 215.40455907583237, 255, 0) 100%);
	width: 100%;
	height: 150px;
	position: absolute;
	left: 0;
	top: 0;
	padding: 0 20px;
}

.header-title {
    color: #FFFFFF;
    width: 250px;
    height: 80px;
    font-family: Jaldi;
    font-size: 32px;
    text-shadow: 1px 2px 2px gray;

    position: fixed;
    top: 85px;
    left: 2%;
    transform: translate(0%, -50%);
    text-align: center;
    font-weight: bold;
    letter-spacing: 0;
    z-index: 1;
}

.myPage-text, .calendarPage-text {
    color: rgba(255, 255, 255, 1);
    font-size: 18px;
    cursor: pointer;
    margin-left: 10px; /* 각 버튼 간격 조절 */
    margin-right: 10px;
}

.bi-list-ul{
	line-height: 22px;
	color: rgba(255, 255, 255, 1);
}

.bi-house {
	line-height: 22px;
}

/* 햄버거 */
.mobile-nav-toggle {
    color: rgba(255, 255, 255, 1);
    font-size: 2rem;
    cursor: pointer;
	z-index: 100;
	position: relative;
    top: 35%;
    transform: translateY(-100%);
    align-items: center; /* 수직 가운데 정렬 */
}

/* 닫기 버튼*/
.mobile-nav-hide {
    color: rgba(255, 255, 255, 1);
    font-size: 2rem;
    cursor: pointer;
}

/* 화면이 작을 때(모바일 화면) 스타일 */
@media (max-width: 1270px) {
    /* 텍스트 버튼 숨김 */
    .navbar .myPage-text{
        display: none;
    }
    .navbar .calendarPage-text{
        display: none;
    }
    /* 햄버거 버튼 표시 */
    .mobile-nav-toggle {
        display: block;
    }
}

/* 화면이 클 때(데스크탑 화면) 스타일 */
@media (min-width: 1270px) {
    /* 텍스트 버튼 표시 */
    .navbar {
        display: flex;
    }
    /* 햄버거 버튼 숨김 */
    .mobile-nav-toggle {
        display: none;
    }
}


