/* ====== 캘린더 페이지  ====== */

* {
    font-family: sans-serif;
    box-sizing: border-box;
}

.calendar {
    width: 90%;
	height:100%;
    margin: auto;
}

.calendar_header {
    display: inline-block;
	padding-top: 110px;
	padding-left: 530px;
}

.calendar_nav {
    display: flex;
    flex-grow: 0;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    font-size: 50px;
}

.go-today {
    width: 80%;
}

.calendar_main {
    border-collapse: collapse;
    width: 80%;
	padding-left: 250px;
}

.days {
    display: flex;
    margin: 25px 0 10px;
}

.day {
    width: calc(100% / 7);
    text-align: center;
    transition: all 0.3s;
}

.dates {
    display: flex;
    flex-flow: row wrap;
    border: 1px solid #33333341;
}

.date {
    cursor: pointer;
    width: calc(100% / 7);
    padding: 5% 1%;
    text-align: center;
    border: 1px solid #33333341;
    transition: all 0.3s;
}

.date-itm {
    display: inline-block;
    vertical-align: middle;
}

.date_event {
    display: inline-block;
    vertical-align: middle;
}

.event-itm {
    display: block;
    font-size: 15px;
}

.date:nth-child(7n + 1),
.day:nth-child(7n + 1) {
    color: #D13E3E;
}

.date:nth-child(7n),
.day:nth-child(7n) {
    color: #396EE2;
}

.other {
    color: rgba(88, 88, 88, 0.315) !important;
}

.today {
    position: relative;
    background-color: rgba(28, 213, 255, 0.627);
    /* 28, 191, 255, 1 */
    color: #ffffff !important;
}

.selected {
    background-color: #99e3ec;
    color: #ffffff !important;
}

.resv-wrapper {
    z-index: 10;
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #3333335b;
    display: none;
    justify-content: center;
}

.resv-wrapper.open {
    display: flex;
}

.resv-bg {
    width: 90%;
