.toggle-switch input {
    -webkit-appearance: none;
    appearance: none;
    -webkit-border-radius: 0;
    border-radius: 0;
}

#container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.toggle-switch input[type=checkbox] {
    display: none;
}

.toggle-track {
    display: inline-block;
    position: relative;
    width: 60px;
    height: 25px;
    border-radius: 60px;
    background: #8b8b8b;
}
.toggle-track:before {
    content: '';
    display: block;
    position: absolute;
    top: -6px;
    left: -15px;
    width: 27px;
    height: 27px;
    margin: 5px;
    background: #fff;
    border-radius: 100%;
    border: 1px solid #8b8b8b;
    transition: left 0.3s;
}
.toggle-switch input[type=checkbox] + label .toggle-track:after {
    content: 'OFF';
    display: inline-block;
    position: absolute;
    right: 8px;
    color: #fff;
}

.toggle-switch input[type=checkbox]:checked + label .toggle-track {
    background: #FA9AA6;
}
.toggle-switch input[type=checkbox]:checked + label .toggle-track:before {
    left: 40px;
    border: 1px solid #FA9AA6;
}
.toggle-switch input[type=checkbox]:checked + label .toggle-track:after {
    content: 'ON';
    left: 5px;
}

